import axios from "axios";
const API_URL = "http://localhost:8080/api/demo/authentication/";

const registerAccount = (username, emailaddress, password, profile) => {
  return axios
    .post(API_URL + 'account/register', { username, emailaddress, password, profile });
}

const login = (usernameoremailaddress, password) => {
  return axios
    .post(API_URL + "account/login", { usernameoremailaddress, password }).then((response) => {
      if (response) {
        localStorage.setItem("account", JSON.stringify(response.data));
      }
      return response;
    });
}

export default {
  registerAccount,
  login
};