import React, { Fragment, useState, useEffect, useRef, useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import jwt_decode from "jwt-decode";
import AccountService from "../services/AccountService";
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { InputText } from "primereact/inputtext";
import { Panel } from 'primereact/panel';
import { Toast } from "primereact/toast";

const messageToast = (toast, severity, summary, detail) => {
    toast.current.show({ severity: severity, summary: summary, detail: detail, life: 3000 });
}

const FormChangePasswordAccount = (props) => {

    const history = useHistory();
    const toast = useRef(null);
    let _token = props.token;
    const [currentAccountId, setCurrentAccountId] = useState('');
    // functions to build form returned by useForm() hook
    const { register, control, errors, handleSubmit, reset, setValue, getValues, watch } = useForm();
    const password = useRef({});
    password.current = watch("newpassword", "");
    /* Setup controls */
    const [isValidToken, setIsValidToken] = useState(false);
    const [changePasswordDialog, setChangePasswordDialog] = useState(false);
    const [passwordShown, setPasswordShown] = useState(false);

    useEffect(() => {
        // do the your logic here
        if (errors.newpassword) {
            console.log(errors.newpassword);
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
        } else if (errors.verifypassword) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors.verifypassword);
        }
    }, [errors]); //  
    //  errors will not trigger the useEffect  

    const checkToken = useCallback(() => {
        return AccountService.checkToken(_token)
            .then((response) => {
                if (response.status >= 200 && response.status < 300) {
                    setIsValidToken(true);
                    // const _token = currentToken.replace('Bearer ', '');
                    const tokenDecoded = jwt_decode(_token);
                    const _currentId = tokenDecoded !== null ? tokenDecoded.id : "0";
                    setCurrentAccountId(_currentId);
                }
            })
            .catch((error) => { /* Handling Axios error in React */
                setIsValidToken(false);
                setCurrentAccountId('');
                if (error.response) {
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("ChangePassword_ByToken_LabelMessageCode400_Content")}`);
                            break;
                        case 401:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode401_Title")}`, `${props.translate("ChangePassword_ByToken_LabelMessageCode401_Content")}`);
                            break;
                        case 403:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode403_Title")}`, `${props.translate("ChangePassword_ByToken_LabelMessageCode403_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("ChangePassword_ByToken_LabelMessageCode404_Content")}`);
                            break;
                        case 410:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode410_Title")}`, `${props.translate("ChangePassword_ByToken_LabelMessageCode410_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser 
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            })
    }, [toast, props.translate, _token])

    useEffect(() => {
        // first time to fetch data
        if (_token === null || _token === undefined) {
            history.push('/error');
        } else {
            checkToken();
        }
    }, [checkToken]);

    const onSubmitChangePassword = (data, e) => {
        changePassword(currentAccountId, data);
    }

    const changePassword = async (currentAccountId, data) => {

        AccountService.changePassword(currentAccountId, data.newpassword)
            .then((response) => {
                if (response.status >= 200 && response.status < 300) {
                    history.push({
                        pathname: '/', state: { isShowToastChangePassword: true }
                    });
                }
            })
            .catch((error) => { /* Handling Axios error in React */
                reset();
                if (error.response) {
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("LabelMessageCode400_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("ChangePassword_LabelMessageCode404_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser 
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    }

    const openChangePasswordDialog = () => {
        setChangePasswordDialog(true);
    }

    const hideChangePasswordDialog = () => {
        setChangePasswordDialog(false);
    }

    const changePasswordDialogFooter = (
        <React.Fragment>
            <Button type="button" label={props.translate("LabelBtn_Cancel")} icon="pi pi-times" className="p-button-outlined p-button-danger" onClick={hideChangePasswordDialog} />
            <Button type="submit" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-success p-button-outlined" autoFocus />
        </React.Fragment>
    )

    const togglePasswordVisiblity = () => {
        setPasswordShown(passwordShown ? false : true);
    };

    return (
        <Fragment>
            <Toast ref={toast} />
            {isValidToken &&
                <div className="p-grid">
                    <div className="p-col-12">
                        <div className="card">
                            {/* "handleSubmit" will validate your inputs before invoking "onSubmit" */}
                            <form onSubmit={handleSubmit(onSubmitChangePassword)}>
                                <Panel header={<h2>{props.translate("ChangePassword_LabelPanel_Title")}</h2>}>
                                    <div className="p-grid p-fluid">
                                        <div className="p-col-12 p-md-6">
                                            <div className="p-inputgroup">
                                                <span className="p-inputgroup-addon">
                                                    <i className="fa fa-fw fa-key"></i>
                                                </span>
                                                <Controller
                                                    name="newpassword"
                                                    control={control}
                                                    defaultValue=""
                                                    rules={{ required: true, pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{1,8}$/i }}
                                                    render={({ onChange, value }) => <InputText type={passwordShown ? "text" : "password"} className="p-inputtext p-widget p-state-default p-corner-all" tooltip={props.translate("ChangePassword_LabelNewPassword_Title1")} tooltipOptions={{ position: 'top' }}
                                                        onChange={onChange} value={value} maxLength={8} placeholder={props.translate("ChangePassword_LabelNewPassword_Subtitle1")} />}
                                                />
                                                <span className="p-buttonset">
                                                    <Button type="button" label={passwordShown ? `${props.translate("LabelIsShowPassword1")}` : `${props.translate("LabelIsShowPassword2")}`} className="p-button-rounded p-button-secondary"
                                                        icon={passwordShown ? "fa fa-fw fa-eye" : "fa fa-fw fa-eye-slash"} onClick={togglePasswordVisiblity} />
                                                </span>
                                            </div>
                                            <span className="text-danger text-small d-block mb-2">
                                                {errors.newpassword?.type === 'required' && <small id="newpassword1-help" className="p-error p-d-block">{props.translate("ChangePassword_LabelMessageError_NewPasswordContent1")}</small>}
                                                {errors.newpassword?.type === 'pattern' && <small id="newpassword2-help" className="p-error p-d-block">{props.translate("ChangePassword_LabelMessageError_NewPasswordContent2")}</small>}
                                            </span>
                                        </div>
                                        <div className="p-col-12 p-md-6">
                                            <div className="p-inputgroup">
                                                <span className="p-inputgroup-addon">
                                                    <i className="fa fa-fw fa-key"></i>
                                                </span>
                                                <Controller
                                                    name="verifypassword"
                                                    control={control}
                                                    defaultValue=""
                                                    rules={{
                                                        required: true,
                                                        pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{1,8}$/i,
                                                        validate: (value) => {
                                                            if (value === password.current) {
                                                                return true;
                                                            } else {
                                                                return false;
                                                            }
                                                        }
                                                    }}
                                                    render={({ onChange, value }) => <InputText type={passwordShown ? "text" : "password"} className="p-inputtext p-widget p-state-default p-corner-all" tooltip={props.translate("ChangePassword_LabelNewPassword_Title2")} tooltipOptions={{ position: 'top' }}
                                                        onChange={onChange} value={value} maxLength={8} placeholder={props.translate("ChangePassword_LabelNewPassword_Subtitle2")} />}
                                                />
                                            </div>
                                            <span className="text-danger text-small d-block mb-2">
                                                {errors.verifypassword?.type === 'required' && <small id="verifypassword1-help" className="p-error p-d-block">{props.translate("ChangePassword_LabelMessageError_VerifyPasswordContent1")}</small>}
                                                {errors.verifypassword?.type === 'pattern' && <small id="verifypassword2-help" className="p-error p-d-block">{props.translate("ChangePassword_LabelMessageError_VerifyPasswordContent2")}</small>}
                                                {errors.verifypassword?.type === 'validate' && <small id="verifypassword3-help" className="p-error p-d-block">{props.translate("ChangePassword_LabelMessageError_VerifyPasswordContent3")}</small>}
                                            </span>
                                        </div>
                                    </div>
                                    <div className="p-grid">
                                        <div className="p-col-12" style={{ textAlign: 'right' }}>
                                            <Button type="button" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-outlined" onClick={openChangePasswordDialog} />
                                            <Button type="button" label={props.translate("LabelBtn_Reset")} icon="pi pi-check" className="p-button-outlined p-button-secondary" onClick={() => reset()} />
                                        </div>
                                    </div>
                                </Panel>
                                <Dialog id="dlgConfirmation" visible={changePasswordDialog} style={{ width: '450px' }} header={props.translate("LabelCorfirmDialog_Title")} modal className="p-fluid" footer={changePasswordDialogFooter} onHide={hideChangePasswordDialog}>
                                    <div className="p-col-12" style={{ textAlign: 'center' }}>
                                        <h4>{props.translate("ChangePassword_LabelDialog_Content")}</h4>
                                    </div>
                                </Dialog>
                            </form>
                        </div>
                    </div>
                </div>
            }
            {!isValidToken &&
                <div className="p-grid">
                    <div className="p-col-12" style={{ textAlign: 'center' }}>
                        <div className="card">
                            <h1>{props.translate("ActivateAccount_LabelError_Title")}</h1>
                        </div>
                    </div>
                </div>
            }
        </Fragment>
    );
}

export default FormChangePasswordAccount;