import React, { useState, useEffect, useRef, useCallback, Fragment } from 'react';
import { withTranslation } from "react-i18next";

import FormChangePasswordAccount from '../components/FormChangePasswordAccount';

const ChangePasswordAccount = (props) => {

    const { t } = props;
    const { match: { params } } = props;

    return (
        <Fragment>
            <FormChangePasswordAccount translate={t} token={params.token}></FormChangePasswordAccount>
        </Fragment >
    );
}

export default (withTranslation()(ChangePasswordAccount));