import React, { Fragment, useState, useEffect, useCallback, useRef } from "react";
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import AccountService from '../services/AccountService';
import { Button } from "primereact/button";
import { Dialog } from 'primereact/dialog';
import { InputText } from "primereact/inputtext";
import { Toast } from "primereact/toast";
import { Panel } from 'primereact/panel';

const messageToast = (toast, severity, summary, detail) => {
    toast.current.show({ severity: severity, summary: summary, detail: detail, life: 3000 });
}

const FormProfileAccount = (props) => {

    const history = useHistory();
    const toast = useRef(null);
    const [currentId, setCurrentId] = useState('');
    // functions to build form returned by useForm() hook
    const { register, control, errors, handleSubmit, reset, setValue, getValues } = useForm();
    /* Setup controls */
    const disabled = useRef(false);
    const [updateAccountDialog, setUpdateAccountDialog] = useState(false);

    const getListAccountById = useCallback(() => {
        return AccountService.getAccountById(props._currentId)
            .then((response) => {
                if (response.status >= 200 && response.status < 300) {
                    setCurrentId(props._currentId);
                    setValue("username", response.data[0].username);
                    setValue("emailaddress", response.data[0].emailaddress);
                    setValue("accounttype", response.data[0].accounttype);
                }
            })
            .catch((error) => { /* Handling Axios error in React */
                if (error.response) {
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("Profile_LabelMessageCode400_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("Profile_LabelMessageCode404_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser 
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            })
    }, [toast, props.translate, props._currentId])

    useEffect(() => {
        // first time to fetch data
        if (props._currentId === null || props._currentId === undefined) {
            history.push('/error');
        } else {
            getListAccountById();
        }
    }, [getListAccountById]);

    useEffect(() => {
        // do the your logic here
        if (errors.username) {
            console.log(errors.username);
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
        } else if (errors.emailaddress) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors.emailaddress);
        }
    }, [errors]); //  
    //  errors will not trigger the useEffect  

    const onSubmitUpdate = (data, e) => {
        updateAccount(currentId, data);
    }

    const updateAccount = async (currentId, data) => {
        AccountService.updateAccount(currentId, data.username, data.emailaddress)
            .then((response) => {
                if (response !== null) {
                    if (response.status >= 200 && response.status < 300) {
                        setDisplayResponsive(false);
                        messageToast(toast, 'success', `${props.translate("LabelMessageCode202_Title")}`, `${props.translate("ProfileUpdate_LabelMessageCode202_Content")}`);
                    }
                }
            })
            .catch((error) => {
                if (error.response) { /* Handling Axios error in React */
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("ProfileUpdate_LabelMessageCode400_Content")}`);
                            break;
                        case 401:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode401_Title")}`, `${props.translate("ProfileUpdate_LabelMessageCode401_Content")}`);
                            break;
                        case 403:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode403_Title")}`, `${props.translate("ProfileUpdate_LabelMessageCode403_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("ProfileUpdate_LabelMessageCode404_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser 
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    };

    const openUpdateAccountDialog = () => {
        setUpdateAccountDialog(true);
    }

    const hideUpdateAccountDialog = () => {
        setUpdateAccountDialog(false);
    }

    const updateAccountDialogFooter = (
        <React.Fragment>
            <Button type="button" label={props.translate("LabelBtn_Cancel")} icon="pi pi-times" className="p-button-outlined p-button-danger" onClick={() => hideUpdateAccountDialog()} />
            <Button type="submit" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-outlined p-button-success" autoFocus />
        </React.Fragment>
    )

    return (
        <Fragment>
            <Toast ref={toast} />
            <div className="p-grid">
                <div className="p-col-12">
                    <div className="card">
                        <form onSubmit={handleSubmit(onSubmitUpdate)}>
                            <Panel header={<h2>{props.translate("Profile_Label_Title")}</h2>}>
                                <div className="p-col-12" style={{ textAlign: 'center' }}>
                                    <h3>{props.translate("Profile_Label_Subtitle")}</h3>
                                </div>
                                <div className="p-grid p-fluid">

                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-user"></i>
                                            </span>
                                            <Controller
                                                name="username"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true, minLength: 8, pattern: /^[A-Za-z]+$/i }}
                                                render={({ onChange, value }) => <InputText type="text" className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} value={value} maxLength={20}
                                                    tooltip={props.translate("Profile_LabelUsername_Title")} tooltipOptions={{ position: 'top' }} placeholder={props.translate("LabelUsername_Subtitle")} />}
                                            />
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.username?.type === 'required' && <small id="username1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent1")}</small>}
                                            {errors.username?.type === 'minLength' && <small id="username2-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent2")}</small>}
                                            {errors.username?.type === 'pattern' && <small id="username3-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent3")}</small>}
                                        </span>
                                    </div>
                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-at"></i>
                                            </span>
                                            <Controller
                                                name="emailaddress"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true, pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i }}
                                                render={({ onChange, value }) => <InputText type="text" className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} value={value} maxLength={30}
                                                    tooltip={props.translate("Profile_LabelEmailAddress_Title")} tooltipOptions={{ position: 'top' }} placeholder={props.translate("LabelEmailAddress_Subtitle")} />}
                                            />
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.emailaddress?.type === 'required' && <small id="emailAddress1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_EmailAddressContent1")}</small>}
                                            {errors.emailaddress?.type === 'pattern' && <small id="emailAddress2-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_EmailAddressContent2")}</small>}
                                        </span>
                                    </div>
                                </div>
                                <div className="p-grid p-fluid">
                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="pi pi-id-card"></i>
                                            </span>
                                            <Controller
                                                name="accounttype"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true }}
                                                render={({ onChange, value }) => <input type="text" className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} value={value} disabled={disabled} placeholder={props.translate("Profile_LabelAccountType_Subtitle")} />}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="p-grid" >
                                    <div className="p-col-12" style={{ textAlign: 'right' }}>
                                        <Button type="button" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-outlined" onClick={openUpdateAccountDialog} />
                                    </div>
                                </div>
                            </Panel>
                            <Dialog id="dlgConfirmation" visible={updateAccountDialog} style={{ width: '450px' }} header={props.translate("LabelCorfirmDialog_Title")} modal className="p-fluid" footer={updateAccountDialogFooter} onHide={hideUpdateAccountDialog}>
                                <div className="p-col-12" style={{ textAlign: 'center' }}>
                                    <h4>{props.translate("Profile_UpdateAccount_Title")}</h4>
                                </div>
                            </Dialog>
                        </form>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default FormProfileAccount;