import React, { useState, useEffect, useRef, useCallback, Fragment } from 'react';
import { withTranslation } from 'react-i18next';
import FormLoginAccount from '../components/FormLoginAccount';

const LoginAccount = (props) => {

    const { t } = props;

    return (
        <Fragment>
            <FormLoginAccount translate={t}></FormLoginAccount>
        </Fragment>
    );
}

export default (withTranslation()(LoginAccount));