import React, { useState, useEffect, useRef, useCallback, Fragment } from 'react';
import { Toast } from "primereact/toast";

const messageToast = (toast, severity, summary, detail) => {
    toast.current.show({ severity: severity, summary: summary, detail: detail, life: 3000 });
}

const ContentIndex = (props) => {

    const toast = useRef(null);
    const _isShowToastRegisterAccount = props.isShowToastRegisterAccount;
    const _isShowToastChangePassword = props.isShowToastChangePassword;

    const showToast = useCallback(() => {
        switch (_isShowToastRegisterAccount) {
            case true:
                messageToast(toast, 'success', `${props.translate("LabelMessageCode202_Title")}`, `${props.translate("RegisterAccount_LabelMessageCode202_Content")}`);
                break;
            case undefined:
                break;
            default:
            // do nothing
        }
        switch (_isShowToastChangePassword) {
            case true:
                messageToast(toast, 'success', `${props.translate("LabelMessageCode202_Title")}`, `${props.translate("ChangePassword_LabelMessageCode202_Content")}`);
                break;
            case undefined:
                break;
            default:
            // do nothing
        }
    }, [toast, props.translate, _isShowToastRegisterAccount, _isShowToastChangePassword])

    useEffect(() => {
        // first time to fetch data
        showToast();
    }, [showToast]);

    return (
        <Fragment>
            <Toast ref={toast} />
            <div className="p-grid">
                <div className="p-col-12">
                    <div className="card">
                        <h5>{props.translate("AppTopbar_LabelLanguage_Subtitle1")}</h5>
                        <p>Use this page to start from scratch and place your custom content. Dashboard</p>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default ContentIndex;