import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import jwt_decode from "jwt-decode";

function validateRole(authorities, props, Component) {

    const currentAccount = JSON.parse(localStorage.getItem('account'));
    const currentToken = currentAccount !== null ? currentAccount.token : "anonymous";
    const _currentTokenEncoded = currentToken.replace('Bearer ', '');
    const _currenttokenDecoded = jwt_decode(_currentTokenEncoded);
    const token = _currenttokenDecoded;
    let validUser = false;

    authorities.forEach(authority => {
        token.authorities.forEach(currentRole => {
            if (currentRole.authority === authority) {
                validUser = true;
            }
        });
    });

    try {
        if (validUser === true) {
            return <Component {...props} />
        } else if (validUser !== true) {
            return <Redirect to={{ pathname: '/dashboard' }} />
        }
    } catch (error) {
        return <Component {...props} />;
    }

}

const CustomRouterPrivate = ({ grantedAuthorities: authorities, component: Component, ...rest }) => {
    return <Route {...rest} render={props => (
        localStorage.getItem('account')
            ? validateRole(authorities, props, Component)
            : <Redirect to={{ pathname: '/login', state: { from: props.location } }}
            />
    )} />
}

export {
    CustomRouterPrivate
};