import React from 'react';
import { Route, Redirect } from 'react-router-dom';

const currentRole = "ANONYMOUS";

function validateRole(authorities, props, Component) {

    let validUser = false;

    authorities.forEach(authority => {
        if (currentRole === authority) {
            validUser = true;
        }
    });

    try {
        if (validUser === true) {
            return <Component {...props} />
        } else if (validUser !== true) {
            return <Redirect to={{ pathname: '/' }} />
        }
    } catch (error) {
        return <Component {...props} />;
    }

}

const CustomRouterPublic = ({ grantedAuthorities: authorities, component: Component, ...rest }) => (
    <Route {...rest} render={(props) => (
        currentRole ? validateRole(authorities, props, Component)
            : <Redirect to={{ pathname: '/', state: { from: props.location } }} />
    )} />
)

export {
    CustomRouterPublic
};