import React, { useState, useEffect, useRef, useCallback, Fragment } from 'react';
import { withTranslation } from "react-i18next";
import jwt_decode from "jwt-decode";
import FormProfileAccount from '../components/FormProfileAccount';

const ProfileAccount = (props) => {

    const { t } = props;
    const currentAccount = JSON.parse(localStorage.getItem('account'));
    const _currentToken = currentAccount !== null ? currentAccount.token : "anonymous";
    const tokenEncoded = _currentToken.replace('Bearer ', '');
    const tokenDecoded = jwt_decode(tokenEncoded);
    const _currentId = tokenDecoded !== null ? tokenDecoded.id : "0";

    return (
        <Fragment>
            <FormProfileAccount translate={t} _currentId={_currentId}></FormProfileAccount>
        </Fragment>
    );
}

export default (withTranslation()(ProfileAccount));