import axios from "axios";
import AuthenticationHeader from "../utils/AuthenticationHeader";
const API_URL = "http://localhost:8080/api/demo/";

const activateAccount = (_token) => {
    return axios
        .post(API_URL + 'security/activate/account', {
            token: _token
        });
}

const checkToken = (_token) => {
    return axios
        .post(API_URL + "security/check/token", {
            token: _token
        });
}

const recoveryPassword = (_emailaddress) => {
    return axios
        .post(API_URL + "security/recovery/password", {
            emailaddress: _emailaddress
        });
}


const changePassword = (_currentAccountId, _newPassword) => {
    return axios
        .post(API_URL + "security/change/password", {
            id: _currentAccountId,
            password: _newPassword
        });
}

const getAccountById = (id) => {
    return axios
        .get(API_URL + "catalog/account/" + id, { headers: AuthenticationHeader() });
}

const updateAccount = (_id, _username, _emailaddress) => {
    return axios
        .put(API_URL + "account/profile/update", {
            id: _id,
            username: _username,
            emailaddress: _emailaddress
        }, {
            headers: AuthenticationHeader()
        });
}

export default {
    activateAccount,
    checkToken,
    recoveryPassword,
    changePassword,
    getAccountById,
    updateAccount
};