import React, { Fragment, useState, useEffect, useRef } from "react";
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import AuthenticationService from "../services/AuthenticationService";
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from "primereact/inputtext";
import { Panel } from 'primereact/panel';
import { Toast } from "primereact/toast";

const messageToast = (toast, severity, summary, detail) => {
    toast.current.show({ severity: severity, summary: summary, detail: detail, life: 3000 });
}

const FormRegisterAccount = (props) => {

    const history = useHistory();
    const translate = props.translate;
    const toast = useRef(null);
    // functions to build form returned by useForm() hook
    const { register, control, errors, handleSubmit, reset, setValue, getValues } = useForm();
    /* Setup controls */
    const [newAccountDialog, setNewAccountDialog] = useState(false);
    const [passwordShown, setPasswordShown] = useState(false);

    useEffect(() => {
        // do the your logic here
        if (errors.username) {
            console.log(errors.username);
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
        } else if (errors.emailAddress) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors.emailAddress);
        } else if (errors.password) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors.password);
        } else if (errors.profile) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors.profile);
        }
    }, [errors]); //  
    //  errors will not trigger the useEffect  

    const dropdownProfiles = [
        {
            "name": "ROLE_ADMIN",
            "description": "Administrator"
        },
        {
            "name": "ROLE_USER",
            "description": "User"
        }
    ];

    const togglePasswordVisiblity = () => {
        setPasswordShown(passwordShown ? false : true);
    };

    const onSubmitRegister = (data, e) => {
        registerAccount(data);
    }

    const registerAccount = async (data) => {
        AuthenticationService.registerAccount(data.username, data.emailAddress, data.password, data.profile.name)
            .then((response) => {
                if (response.status >= 200 && response.status < 300) {
                    setNewAccountDialog(false);
                    history.push({
                        pathname: '/home', state: { isShowToastRegisterAccount: true }
                    });
                }
            }).catch((error) => {
                if (error.response) {
                    reset();
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("LabelMessageCode400_Content")}`);
                            break;
                        case 401:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode401_Title")}`, `${props.translate("RegisterAccount_LabelMessageCode401_Content")}`);
                            break;
                        case 403:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode403_Title")}`, `${props.translate("RegisterAccount_LabelMessageCode403_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("RegisterAccount_LabelMessageCode404_Content")}`);
                            break;
                        case 405:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode405_Title")}`, `${props.translate("RegisterAccount_LabelMessageCode405_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    }
    const openCreateAccountDialog = () => {
        setNewAccountDialog(true);
    }

    const hideCreaterAccountDialog = () => {
        setNewAccountDialog(false);
    }

    const newAccountDialogFooter = (
        <React.Fragment>
            <Button type="button" label={props.translate("LabelBtn_Cancel")} icon="pi pi-times" className="p-button-outlined p-button-danger" onClick={() => hideCreaterAccountDialog()} />
            <Button type="submit" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-success p-button-outlined" autoFocus />
        </React.Fragment>
    )

    return (
        <Fragment>
            <Toast ref={toast} />
            <div className="p-grid">
                <div className="p-col-12">
                    <div className="card">
                        {/* "handleSubmit" will validate your inputs before invoking "onSubmit" */}
                        <form onSubmit={handleSubmit(onSubmitRegister)}>
                            <Panel header={<h2>{props.translate("RegisterAccount_LabelPanel_Title")}</h2>}>
                                <div className="p-grid p-fluid">

                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-user"></i>
                                            </span>
                                            <Controller
                                                name="username"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true, minLength: 8, pattern: /^[A-Za-z]+$/i }}
                                                render={({ onChange, value }) => <InputText type="text" className="p-inputtext p-widget p-state-default p-corner-all" tooltip={props.translate("LabelUsername_Title")} tooltipOptions={{ position: 'top' }}
                                                    onChange={onChange} value={value} maxLength={20} placeholder={props.translate("LabelUsername_Subtitle")} />}
                                            />
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.username?.type === 'required' && <small id="username1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent1")}</small>}
                                            {errors.username?.type === 'minLength' && <small id="username2-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent2")}</small>}
                                            {errors.username?.type === 'pattern' && <small id="username3-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent3")}</small>}
                                        </span>
                                    </div>

                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-at"></i>
                                            </span>
                                            <Controller
                                                name="emailAddress"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true, pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i }}
                                                render={({ onChange, value }) => <InputText type="text" className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} tooltip={props.translate("LabelEmailAddress_Title")} tooltipOptions={{ position: 'top' }}
                                                    value={value} maxLength={30} placeholder={props.translate("LabelEmailAddress_Subtitle")} />}
                                            />
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.emailAddress?.type === 'required' && <small id="emailAddress1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_EmailAddressContent1")}</small>}
                                            {errors.emailAddress?.type === 'pattern' && <small id="emailAddress2-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_EmailAddressContent2")}</small>}
                                        </span>
                                    </div>

                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-key"></i>
                                            </span>
                                            <Controller
                                                name="password"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true, pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{1,8}$/i }}
                                                render={({ onChange, value }) => <InputText type={passwordShown ? "text" : "password"} className="p-inputtext p-widget p-state-default p-corner-all" tooltip={props.translate("LabelPassword_Title")} tooltipOptions={{ position: 'top' }}
                                                    onChange={onChange} value={value} maxLength={8} placeholder={props.translate("LabelPassword_Subtitle")} />}
                                            />
                                            <span className="p-buttonset">
                                                <Button type="button" label={passwordShown ? `${props.translate("LabelIsShowPassword1")}` : `${props.translate("LabelIsShowPassword2")}`} className="p-button-rounded p-button-secondary"
                                                    icon={passwordShown ? "fa fa-fw fa-eye" : "fa fa-fw fa-eye-slash"} onClick={togglePasswordVisiblity} />
                                            </span>
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.password?.type === 'required' && <small id="profile1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_PasswordContent1")}</small>}
                                            {errors.password?.type === 'pattern' && <small id="profile1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_PasswordContent2")}</small>}
                                        </span>
                                    </div>

                                    <div className="p-col-12 p-md-6">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">{props.translate("RegisterAccount_LabelSelect_Title")}</span>
                                            <Controller
                                                name="profile"
                                                control={control}
                                                defaultValue={""}
                                                rules={{ required: true }}
                                                render={(props) => (
                                                    <Dropdown id="profile" value={props.value} onChange={(e) => props.onChange(e.value)} options={dropdownProfiles} optionLabel="description"
                                                        placeholder={translate("RegisterAccount_LabelSelect_Title")} />
                                                )} />
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.profile?.type === 'required' && <small id="profile1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_ProfileNameContent1")}</small>}
                                        </span>
                                    </div>

                                </div>
                                <div className="p-grid">
                                    <div className="p-col-12" style={{ textAlign: 'right' }}>
                                        <Button type="button" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-outlined" onClick={openCreateAccountDialog} />
                                        <Button type="button" label={props.translate("LabelBtn_Reset")} icon="pi pi-check" className="p-button-outlined p-button-secondary" onClick={() => reset()} />
                                    </div>
                                </div>
                            </Panel>
                            <Dialog id="dlgConfirmation" visible={newAccountDialog} style={{ width: '450px' }} header={props.translate("LabelCorfirmDialog_Title")} modal className="p-fluid" footer={newAccountDialogFooter} onHide={hideCreaterAccountDialog}>
                                <div className="p-col-12" style={{ textAlign: 'center' }}>
                                    <h4>{props.translate("RegisterAccount_LabelDialog_Content")}</h4>
                                </div>
                            </Dialog>
                        </form>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default FormRegisterAccount;