import React from 'react';
import { useHistory } from 'react-router-dom';
import classNames from 'classnames';

const AppTopbar = (props) => {

    const history = useHistory();

    const goDashboard = () => {
        history.push('/');
    };

    const topbarItemsClassName = classNames('topbar-items fadeInDown', { 'topbar-items-visible': props.topbarMenuActive });
    const profileItemClassName = classNames('profile-item', { 'active-top-menu': props.activeTopbarItem === 'profile' });

    return (
        <div className="topbar clearfix">
            <div className="topbar-left">
                <button className="p-link" onClick={goDashboard}>
                    <img alt="Logo" src={require('./assets/layout/images/logo.png').default} className="topbar-logo" />
                </button>
            </div>

            <div className="topbar-right">
                <button className="p-link" id="menu-button" onClick={props.onMenuButtonClick}>
                    <i className="pi pi-angle-left"></i>
                </button>

                <button className="p-link" id="topbar-menu-button" onClick={props.onTopbarMenuButtonClick}>
                    <i className="pi pi-bars"></i>
                </button>
                <ul className={topbarItemsClassName}>

                    <li className={classNames({ 'active-top-menu': props.activeTopbarItem === 'language' })} onClick={(e) => props.onTopbarItemClick({ originalEvent: e, item: 'language' })}>
                        <button className="p-link">
                            <i className="topbar-icon fa fa-fw fas fa-language"></i>
                            <span className="topbar-item-name">{props.translate("AppTopbar_LabelLanguage_Title")}</span>
                        </button>
                        <ul className="layout-menu fadeInDown">
                            <li role="menuitem">
                                <button className="p-link" onClick={props.onChangeLanguageSpanishClick}>
                                    <i className="fa fa-fw fas fa-chevron-right"></i>
                                    <span>{props.translate("AppTopbar_LabelLanguage_Subtitle1")}</span>
                                </button>
                            </li>
                            <li role="menuitem">
                                <button className="p-link" onClick={props.onChangeLanguageEnglishClick}>
                                    <i className="fa fa-fw fas fa-chevron-right"></i>
                                    <span>{props.translate("AppTopbar_LabelLanguage_Subtitle2")}</span>
                                </button>
                            </li>
                        </ul>
                    </li>

                    {props.isValidLogin === false ? (
                        <>
                            <li className={classNames({ 'active-top-menu': props.activeTopbarItem === 'settings' })} onClick={(e) => props.onTopbarItemClick({ originalEvent: e, item: 'settings' })}>
                                <button className="p-link">
                                    <i className="topbar-icon fa fa-fw fa-th-large"></i>
                                    <span className="topbar-item-name">{props.translate("AppTopbar_LabelAccount_Title")}</span>
                                </button>
                                <ul className="layout-menu fadeInDown">
                                    <li role="menuitem">
                                        <button className="p-link" onClick={() => history.push('/login')}>
                                            <i className="pi pi-fw pi-sign-in"></i>
                                            <span>{props.translate("LabelLogIn")}</span>
                                        </button>
                                    </li>
                                </ul>
                            </li>
                        </>
                    ) : (
                        <>
                            {
                                (props.profileMode === 'top' || props.horizontal) && (
                                    <li className={profileItemClassName} onClick={(e) => props.onTopbarItemClick({ originalEvent: e, item: 'profile' })}>
                                        <button className="p-link">
                                            <img alt="User" className="profile-image" src={require('./assets/layout/images/login/icon-login.svg').default} />
                                            <span className="topbar-item-name">Isabel Lopez</span>
                                            <span className="topbar-item-role">Marketing</span>
                                        </button>
                                        <ul className="layout-menu fadeInDown">
                                            <li role="menuitem">
                                                <button className="p-link" onClick={() => history.push('/profile')}>
                                                    <i className="pi pi-fw pi-user"></i>
                                                    <span>{props.translate("LabelProfile")}</span>
                                                </button>
                                            </li>
                                            <li role="menuitem">
                                                <button className="p-link">
                                                    <i className="pi pi-fw pi-cog"></i>
                                                    <span>{props.translate("LabelSettings")}</span>
                                                </button>
                                            </li>
                                            <li role="menuitem">
                                                <button className="p-link">
                                                    <i className="pi pi-fw pi-sign-out"></i>
                                                    <span>{props.translate("LabelLogOut")}</span>
                                                </button>
                                            </li>
                                        </ul>
                                    </li>)
                            }
                            <li className={classNames({ 'active-top-menu': props.activeTopbarItem === 'notifications' })} onClick={(e) => props.onTopbarItemClick({ originalEvent: e, item: 'notifications' })}>
                                <button className="p-link">
                                    <i className="topbar-icon pi pi-fw pi-bell"></i>
                                    <span className="topbar-badge animated rubberBand">1</span>
                                    <span className="topbar-item-name">Notifications</span>
                                </button>

                                <ul className="layout-menu fadeInDown">
                                    <li role="menuitem">
                                        <button className="p-link">
                                            <i className="pi pi-fw pi-sliders-h"></i>
                                            <span>Pending tasks</span>
                                        </button>
                                    </li>
                                </ul>
                            </li>
                        </>
                    )}

                    <li className={classNames('search-item', { 'active-top-menu': props.activeTopbarItem === 'search' })} onClick={(e) => props.onTopbarItemClick({ originalEvent: e, item: 'search' })}>
                        <div className="topbar-search">
                            <input type="text" placeholder="Search" />
                            <i className="pi pi-search"></i>
                        </div>
                    </li>

                </ul>
            </div>
        </div>
    );
}

export default AppTopbar;