import React, { useState, useEffect, useRef, useCallback } from 'react';
import { Redirect, Route, Switch, BrowserRouter, useHistory } from 'react-router-dom';
import classNames from 'classnames';
import PrimeReact from 'primereact/utils';
import AppTopBar from './AppTopbar';
import AppFooter from './AppFooter';
import AppMenu from './AppMenu';
import AppConfig from './AppConfig';
import AppInlineProfile from './AppInlineProfile';

const AppHome = (props) => {

    const history = useHistory();
    const [menuActive, setMenuActive] = useState(false);
    const [profileMode, setProfileMode] = useState('inline');
    const [profileExpanded, setProfileExpanded] = useState(false);
    const [menuMode, setMenuMode] = useState('static');
    const [overlayMenuActive, setOverlayMenuActive] = useState(false);
    const [staticMenuDesktopInactive, setStaticMenuDesktopInactive] = useState(false);
    const [staticMenuMobileActive, setStaticMenuMobileActive] = useState(false);
    const [topbarMenuActive, setTopbarMenuActive] = useState(false);
    const [activeTopbarItem, setActiveTopbarItem] = useState(null);
    const [darkMenu, setDarkMenu] = useState(false);
    const [theme, setTheme] = useState('blue');
    const [layout, setLayout] = useState('blue');
    const [configActive, setConfigActive] = useState(false);
    const [inputStyle, setInputStyle] = useState('outlined');
    const [ripple, setRipple] = useState(false);
    const [isValidLogin, setIsValidLogin] = useState(false);
    const [menuData, setMenuData] = useState([]);

    let menuClick = false;
    let topbarItemClick = false;
    let configClick = false;

    const currentAccountData = useCallback(() => {

        props.currentAccountRole.forEach(item => {

            if (item.authority === 'ANONYMOUS') {
                setIsValidLogin(false);
                const menuAnnonymous = [
                    {
                        label: `${props.translate('AppMenuPublic_LabelPages_Title1')}`, icon: 'pi pi-fw pi-home',
                        items: [
                            { label: `${props.translate('AppMenuPublic_LabelPages_Subtitle1')}`, icon: 'pi pi-fw pi-home', to: '/home' },
                            { label: `${props.translate('AppMenuPublic_LabelPages_Subtitle2')}`, icon: 'pi pi-fw pi-user-plus', to: '/register' }
                        ]
                    }
                ];
                setMenuData(menuAnnonymous);
            } else {

                setIsValidLogin(true);
                let _role;
                if (props.currentAccountRole.length > 1) {
                    _role = [];
                    props.currentAccountRole.forEach(role => {
                        _role.push(role.authority);
                    });
                } else {
                    _role = item.authority
                }

                if ('ROLE_ADMIN' === _role) {
                    const menuAdministrator = [
                        {
                            label: `${props.translate("AppMenuPrivate_LabelPages_Title1")}`, icon: 'pi pi-fw pi-home',
                            items: [
                                { label: `${props.translate("AppMenuPrivate_LabelPages_Subtitle1")}`, icon: 'pi pi-fw pi-globe', to: '/dashboard' }
                            ]
                        }

                    ];
                    setMenuData(menuAdministrator)
                }
                else if ('ROLE_USER' === _role) {
                    const menuUser = [
                        {
                            label: `${props.translate("AppMenuPrivate_LabelPages_Title1")}`, icon: 'pi pi-fw pi-home',
                            items: [
                                { label: `${props.translate("AppMenuPrivate_LabelPages_Subtitle1")}`, icon: 'pi pi-fw pi-globe', to: '/dashboard' }
                            ]
                        }
                    ];
                    setMenuData(menuUser);
                }
                else if (_role.length > 1) {

                }
            }

        });
    }, [props.translate, props.currentAccountRole])

    useEffect(() => {
        // first time to fetch data
        currentAccountData();

    }, [currentAccountData]);


    const onInputStyleChange = (inputStyle) => {
        setInputStyle(inputStyle);
    };

    const onRippleChange = (e) => {
        PrimeReact.ripple = e.value;
        setRipple(e.value);
    };

    const onDocumentClick = () => {
        if (!topbarItemClick) {
            setActiveTopbarItem(null);
            setTopbarMenuActive(false);
        }

        if (!menuClick) {
            if (isHorizontal() || isSlim()) {
                setMenuActive(false);
            }

            hideOverlayMenu();
        }

        if (configActive && !configClick) {
            setConfigActive(false);
        }

        menuClick = false;
        topbarItemClick = false;
        configClick = false;
    };

    const onProfileClick = (e) => {
        setProfileExpanded(prevProfileExpanded => !prevProfileExpanded);
    };

    const onMenuClick = () => {
        menuClick = true;
    };

    const onMenuitemClick = (event) => {
        if (!event.item.items) {
            hideOverlayMenu();
        }

        if (!event.item.items && (isSlim() || isHorizontal())) {
            setMenuActive(false);
        }
    };

    const onRootMenuitemClick = () => {
        setMenuActive(prevMenuActive => !prevMenuActive);
    };

    const onMenuButtonClick = (event) => {
        menuClick = true;
        setTopbarMenuActive(false);

        if (menuMode === 'overlay') {
            setOverlayMenuActive(prevOverlayMenuActive => !prevOverlayMenuActive);
        }
        else {
            if (isDesktop())
                setStaticMenuDesktopInactive(prevStaticMenuDesktopInactive => !prevStaticMenuDesktopInactive);
            else
                setStaticMenuMobileActive(prevStaticMenuMobileActive => !prevStaticMenuMobileActive);
        }

        event.preventDefault();
    };

    const onTopbarMenuButtonClick = (event) => {
        topbarItemClick = true;
        setTopbarMenuActive(prevTopbarMenuActive => !prevTopbarMenuActive);
        hideOverlayMenu();
        event.preventDefault();
    };

    const onTopbarItemClick = (e) => {
        topbarItemClick = true;

        if (activeTopbarItem === e.item)
            setActiveTopbarItem(null)
        else
            setActiveTopbarItem(e.item);

        e.originalEvent.preventDefault();
    };

    const hideOverlayMenu = () => {
        setOverlayMenuActive(false);
        setStaticMenuMobileActive(false);
    };

    const isDesktop = () => {
        return window.innerWidth > 1024;
    };

    const isHorizontal = () => {
        return menuMode === 'horizontal';
    };

    const isSlim = () => {
        return menuMode === 'slim';
    };

    const isMenuVisible = () => {
        if (isDesktop()) {
            if (menuMode === 'static')
                return !staticMenuDesktopInactive;
            else if (menuMode === 'overlay')
                return overlayMenuActive;
            else
                return true;
        } else {
            if (staticMenuMobileActive)
                return true;
            else if (menuMode === 'overlay')
                return overlayMenuActive;
            else
                return true;
        }
    };

    const onMenuModeChange = (e) => {
        setMenuMode(e.value);
        setStaticMenuDesktopInactive(false);
        setOverlayMenuActive(false);

        if (e.value === 'horizontal') {
            setProfileMode('top');
        }
    };

    const onMenuColorChange = (e) => {
        setDarkMenu(e.value);
    };

    const onProfileModeChange = (e) => {
        setProfileMode(e.value);
    };

    const onConfigClick = () => {
        configClick = true;
    };

    const onConfigButtonClick = () => {
        setConfigActive(prevConfigActive => !prevConfigActive);
        configClick = true;
    };

    const onLayoutColorChange = (color) => {
        setLayout(color);
    };

    const onThemeChange = (color) => {
        setTheme(color);
    };

    let containerClassName = classNames('layout-wrapper', {
        'menu-layout-static': menuMode !== 'overlay',
        'menu-layout-overlay': menuMode === 'overlay',
        'layout-menu-overlay-active': overlayMenuActive,
        'menu-layout-slim': menuMode === 'slim',
        'menu-layout-horizontal': menuMode === 'horizontal',
        'layout-menu-static-inactive': staticMenuDesktopInactive,
        'layout-menu-static-active': staticMenuMobileActive,
        'p-input-filled': inputStyle === 'filled',
        'p-ripple-disabled': !ripple,
    });

    let menuClassName = classNames('layout-menu-container', { 'layout-menu-dark': darkMenu, 'layout-menu-container-inactive': !isMenuVisible() });

    return (
        <div className={containerClassName} onClick={onDocumentClick}>
            <div className="layout-wrapper-content">
                <AppTopBar topbarMenuActive={topbarMenuActive} profileMode={profileMode} horizontal={isHorizontal()} activeTopbarItem={activeTopbarItem}
                    onMenuButtonClick={onMenuButtonClick} onTopbarMenuButtonClick={onTopbarMenuButtonClick} onTopbarItemClick={onTopbarItemClick}
                    onChangeLanguageEnglishClick={props.onChangeLanguageEnglishClick} onChangeLanguageSpanishClick={props.onChangeLanguageSpanishClick} translate={props.translate} isValidLogin={isValidLogin} />

                <div className={menuClassName} onClick={onMenuClick}>
                    <div className="menu-scroll-content">
                        {isValidLogin === false ? (
                            <>
                                <AppMenu model={menuData} menuMode={menuMode} active={menuActive} onMenuitemClick={onMenuitemClick} onRootMenuitemClick={onRootMenuitemClick} />
                            </>
                        ) : (
                            <>
                                {profileMode === 'inline' && menuMode !== 'horizontal' && <AppInlineProfile onProfileClick={onProfileClick} expanded={profileExpanded} translate={props.translate} />}
                                <AppMenu model={menuData} menuMode={menuMode} active={menuActive} onMenuitemClick={onMenuitemClick} onRootMenuitemClick={onRootMenuitemClick} />
                            </>
                        )}
                    </div>
                </div>

                <div className="layout-main">
                    {props.children}
                </div>

                <AppConfig configActive={configActive} menuMode={menuMode} onMenuModeChange={onMenuModeChange} darkMenu={darkMenu} onMenuColorChange={onMenuColorChange}
                    profileMode={profileMode} onProfileModeChange={onProfileModeChange} onConfigClick={onConfigClick} onConfigButtonClick={onConfigButtonClick}
                    layout={layout} onLayoutColorChange={onLayoutColorChange} theme={theme} onThemeChange={onThemeChange}
                    rippleActive={ripple} onRippleChange={onRippleChange} inputStyle={inputStyle} onInputStyleChange={onInputStyleChange} translate={props.translate} isValidLogin={isValidLogin} />

                <div className="layout-mask modal-in" />

                <AppFooter />
            </div>
        </div>
    );
}

export default AppHome;