import React, { useState, useEffect, useRef, useCallback, Fragment } from 'react';
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import AuthenticationService from '../services/AuthenticationService';
import AccountService from '../services/AccountService';
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { Toast } from "primereact/toast";

const messageToast = (toast, severity, summary, detail) => {
    toast.current.show({ severity: severity, summary: summary, detail: detail, life: 3000 });
}

const FormLoginAccount = (props) => {

    const history = useHistory();
    const toast = useRef(null);
    /* Setup controls */
    const [recoveryPasswordDialog, setRecoveryPasswordDialog] = useState(false);
    const [passwordShown, setPasswordShown] = useState(false);
    /* functions to build form returned by useForm() hook */
    const { register, control, errors, handleSubmit, reset } = useForm();
    const { register: register2, control: control2, errors: errors2, handleSubmit: handleSubmit2, reset: reset2 } = useForm();

    useEffect(() => {
        // do the your logic here
        if (errors.usernameoremailaddress) {
            console.log(errors.usernameoremailaddress);
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
        } else if (errors.password) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors.password);
        } else if (errors2.emailAddress) {
            messageToast(toast, 'error', `${props.translate("RegisterAccount_LabelMessageError_Title")}`, `${props.translate("RegisterAccount_LabelMessageError_Content")}`);
            console.log(errors2.password);
        }
    }, [errors]); //  
    //  errors will not trigger the useEffect  


    const onSubmitLogin = (data, e) => {
        login(data);
    };

    const onSubmitRecoveryPassword = (data, e) => {
        recoveryPassword(data);
    };

    const login = async (data) => {
        AuthenticationService.login(data.usernameoremailaddress, data.password)
            .then((response) => {
                if (response.status >= 200 && response.status < 300) {
                    history.push({
                        pathname: '/dashboard', state: { isShowToastLogin: true }
                    });
                }
            })
            .catch((error) => { /* Handling Axios error in React */
                reset();
                if (error.response) {
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("LabelMessageCode400_Content")}`);
                            break;
                        case 401:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode401_Title")}`, `${props.translate("Login_LabelMessageCode401_Content")}`);
                            break;
                        case 403:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode403_Title")}`, `${props.translate("Login_LabelMessageCode403_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("Login_LabelMessageCode404_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser 
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    }

    const recoveryPassword = async (data) => {
        AccountService.recoveryPassword(data.emailAddress)
            .then((response) => {
                if (response !== null) {
                    if (response.status >= 200 && response.status < 300) {
                        setRecoveryPasswordDialog(false);
                        messageToast(toast, 'success', `${props.translate("LabelMessageCode202_Title")}`, `${props.translate("RecoveryPassword_LabelMessageCode202_Content")}`);
                    }
                }
            })
            .catch((error) => {
                reset2();
                if (error.response) { /* Handling Axios error in React */
                    // The request was made and the server responded with a status code
                    const code = error.response.status;
                    switch (code) {
                        case 400:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode400_Title")}`, `${props.translate("RecoveryPassword_LabelMessageCode400_Content")}`);
                            break;
                        case 401:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode401_Title")}`, `${props.translate("RecoveryPassword_LabelMessageCode401_Content")}`);
                            break;
                        case 404:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode404_Title")}`, `${props.translate("RecoveryPassword_LabelMessageCode404_Content")}`);
                            break;
                        case 500:
                            messageToast(toast, 'error', `${props.translate("LabelMessageCode500_Title")}`, `${props.translate("LabelMessageCode500_Content")}`);
                            break;
                        default:
                        // do nothing
                    }
                    return Promise.reject(error);
                } else if (error.request) {
                    // The request was made but no response was received `error.request` is an instance of XMLHttpRequest in the browser 
                    // and an instance of http.ClientRequest in node.js
                    console.log(error.request);
                    return Promise.reject(error);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    }

    const hideRecoveryPasswordDialog = () => {
        setRecoveryPasswordDialog(false);
    }

    const openRecoveryPasswordDialog = () => {
        setRecoveryPasswordDialog(true);
    }

    const recoveryPasswordDialogFooter = (
        <React.Fragment>
            <Button type="button" label={props.translate("LabelBtn_Cancel")} icon="pi pi-times" className="p-button-text p-button-danger" onClick={hideRecoveryPasswordDialog} />
            <Button type="submit" label={props.translate("LabelBtn_Accept")} icon="pi pi-check" className="p-button-text p-button-success" />
        </React.Fragment>
    );

    const togglePasswordVisiblity = () => {
        setPasswordShown(passwordShown ? false : true);
    };

    return (
        <Fragment>
            <Toast ref={toast} />
            <div className="login-body">
                <div className="card login-panel p-fluid" style={{ height: '100%' }}>
                    <form key={1} onSubmit={handleSubmit(onSubmitLogin)}>
                        <div className="login-panel-content">
                            <div className="p-grid">
                                <div className="p-col-3" style={{ textAlign: 'left' }}>
                                    <img src={require('../assets/layout/images/login/icon-login.svg').default} alt="avalon-layout" />
                                </div>
                                <div className="p-col-9" style={{ textAlign: 'left' }}>
                                    <h2 className="welcome-text">{props.translate("Login_Label_Title")}</h2>
                                    <span className="guest-sign-in">{props.translate("Login_Label_Subtitle")}</span>
                                </div>
                                <div className="p-col-12" style={{ textAlign: 'left' }}>
                                    <label className="login-label">{props.translate("LabelUsername_EmailAddress_Title")}</label>
                                    <div className="login-input">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-user"></i>
                                            </span>
                                            <Controller
                                                name="usernameoremailaddress"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true }}
                                                render={({ onChange, value }) => <input type="text" className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} value={value} maxLength={35} placeholder={props.translate("LabelUsername_EmailAddress_Subtitle")} />}
                                            />
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.usernameoremailaddress?.type === 'required' && <small id="usernameoremailaddress1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_UsernameContent1")}</small>}
                                        </span>
                                    </div>
                                </div>
                                <div className="p-col-12" style={{ textAlign: 'left' }}>
                                    <label className="login-label">{props.translate("LabelPassword_Title")}</label>
                                    <div className="login-input">
                                        <div className="p-inputgroup">
                                            <span className="p-inputgroup-addon">
                                                <i className="fa fa-fw fa-key"></i>
                                            </span>
                                            <Controller
                                                name="password"
                                                control={control}
                                                defaultValue=""
                                                rules={{ required: true, pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{1,8}$/i }}
                                                render={({ onChange, value }) => <input type={passwordShown ? "text" : "password"} className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} value={value} maxLength={8} placeholder={props.translate("LabelPassword_Subtitle")} />}
                                            />
                                            <span className="p-buttonset">
                                                <Button type="button" label={passwordShown ? `${props.translate("LabelIsShowPassword1")}` : `${props.translate("LabelIsShowPassword2")}`} className="p-button-rounded p-button-secondary"
                                                    icon={passwordShown ? "fa fa-fw fa-eye" : "fa fa-fw fa-eye-slash"} onClick={togglePasswordVisiblity} />
                                            </span>
                                        </div>
                                        <span className="text-danger text-small d-block mb-2">
                                            {errors.password?.type === 'required' && <small id="profile1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_PasswordContent1")}</small>}
                                            {errors.password?.type === 'pattern' && <small id="profile1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_PasswordContent2")}</small>}
                                        </span>
                                    </div>
                                </div>
                                <div className="p-col-12 p-md-6 button-pane">
                                    <Button type="submit" label={props.translate("Login_LabelBtn_Accept")} />
                                </div>
                                <div className="p-col-12 p-md-6 link-pane">
                                    <button type="button" className="p-link" onClick={openRecoveryPasswordDialog}>{props.translate("Login_LabelRecoveryPW_Title")}</button>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            <form key={2} onSubmit={handleSubmit2(onSubmitRecoveryPassword)}>
                <Dialog visible={recoveryPasswordDialog} style={{ width: '450px' }} header={props.translate("LabelCorfirmDialog_Title")} modal className="p-fluid" footer={recoveryPasswordDialogFooter} onHide={hideRecoveryPasswordDialog}>
                    <div className="p-col-12" style={{ textAlign: 'center' }}>
                        <h4>{props.translate("RecoveryPassword_LabelDialog_Content")}</h4>
                    </div>
                    <div className="p-col-12">
                        <div className="p-inputgroup">
                            <span className="p-inputgroup-addon">
                                <i className="fa fa-fw fa-at"></i>
                            </span>
                            <Controller
                                name="emailAddress"
                                control={control2}
                                defaultValue=""
                                rules={{ required: true, pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i }}
                                render={({ onChange, value }) => <input type="text" className="p-inputtext p-widget p-state-default p-corner-all" onChange={onChange} value={value} maxLength={30} />}
                            />
                        </div>
                        <span className="text-danger text-small d-block mb-2">
                            {errors2.emailAddress?.type === 'required' && <small id="emailAddress1-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_EmailAddressContent1")}</small>}
                            {errors2.emailAddress?.type === 'pattern' && <small id="emailAddress2-help" className="p-error p-d-block">{props.translate("RegisterAccount_LabelMessageError_EmailAddressContent2")}</small>}
                        </span>
                    </div>
                </Dialog>
            </form>
        </Fragment>
    );
}

export default FormLoginAccount;