import React, { useState, useEffect, useRef, useCallback } from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch, useHistory } from 'react-router-dom';
import { CustomRouterPublic } from './utils/CustomRouterPublic';
import { CustomRouterPrivate } from './utils/CustomRouterPrivate';
import { I18nextProvider } from 'react-i18next';
import i18n from './assets/i18n/i18n';
import { withTranslation } from "react-i18next";
import jwt_decode from "jwt-decode";
import AppHome from './AppHome';
import Index from './pages/Index.jsx';
import RegisterAccount from './pages/RegisterAccount';
import ActivateAccount from './pages/ActivateAccount.jsx';
import ChangePasswordAccount from './pages/ChangePasswordAccount';
import Dashboard from './components/Dashboard.jsx';
import ProfileAccount from './pages/ProfileAccount.jsx';
import './assets/layout/css/layout-darkgrey.css';
import './assets/theme/theme-darkgrey.css';
import 'primereact/resources/themes/saga-blue/theme.css';
import 'primereact/resources/primereact.min.css';
import 'fullcalendar/dist/fullcalendar.css';
import 'font-awesome/css/font-awesome.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import NotFound from './pages/NotFound';

const App = (props) => {

    let currentAccount = JSON.parse(localStorage.getItem('account'));
    let _currentAccount = currentAccount !== null ? currentAccount.token : 'ANONYMOUS';
    const { t } = props;
    const [loggedIn, setLoggedIn] = useState(false);
    const [currentAccountRole, setCurrentAccountRole] = useState([]);

    const onChangeLanguageEnglishClick = () => {
        let newlang = 'en';
        props.i18n.changeLanguage(newlang);
    }

    const onChangeLanguageSpanishClick = () => {
        let newlang = 'es';
        props.i18n.changeLanguage(newlang);
    }

    const currentAccountData = useCallback(() => {

        if (_currentAccount === 'ANONYMOUS') {
            const obj = [{ 'authority': _currentAccount }];
            setCurrentAccountRole(obj);
            setLoggedIn(false);
        } else {
            // const admin = "ROLE_ADMIN";
            // const user = "ROLE_USER";
            // const obj = [{ 'authority': admin }, { 'authority': user }];
            let tokenEncoded = _currentAccount.replace('Bearer ', '');
            let tokenDecoded = jwt_decode(tokenEncoded);
            setCurrentAccountRole(tokenDecoded.authorities);
            setLoggedIn(true);
        }
    }, [_currentAccount])

    useEffect(() => {
        // first time to fetch data
        currentAccountData();
    }, [currentAccountData]);

    return (

        <I18nextProvider i18n={i18n}>

            <AppHome translate={t} onChangeLanguageEnglishClick={onChangeLanguageEnglishClick} onChangeLanguageSpanishClick={onChangeLanguageSpanishClick} currentAccountRole={currentAccountRole}>
                <Route
                    render={() => {
                        if (loggedIn === false) {
                            return (
                                <Switch>
                                    <CustomRouterPublic path="/" exact grantedAuthorities={['ANONYMOUS']} component={Index} />
                                    <CustomRouterPublic path="/home" grantedAuthorities={['ANONYMOUS']} component={Index} />
                                    <CustomRouterPublic path="/register" grantedAuthorities={['ANONYMOUS']} component={RegisterAccount} />
                                    <CustomRouterPublic path="/activate/account/:token?" grantedAuthorities={['ANONYMOUS']} component={ActivateAccount} />
                                    <CustomRouterPublic path="/change/password/:token?" grantedAuthorities={['ANONYMOUS']} component={ChangePasswordAccount} />
                                    <CustomRouterPublic grantedAuthorities={['ANONYMOUS']} component={NotFound} />
                                </Switch>
                            )
                        } else if (loggedIn === true) {
                            return (
                                <Switch>
                                    <CustomRouterPrivate path="/" exact grantedAuthorities={['ROLE_ADMIN', 'ROLE_USER']} component={Dashboard} />
                                    <CustomRouterPrivate path="/dashboard" grantedAuthorities={['ROLE_ADMIN', 'ROLE_USER']} component={Dashboard} />
                                    <CustomRouterPrivate path="/profile" grantedAuthorities={['ROLE_ADMIN', 'ROLE_USER']} component={ProfileAccount} />
                                    <CustomRouterPrivate grantedAuthorities={['ROLE_ADMIN', 'ROLE_USER']} component={NotFound} /> */
                                </Switch>
                            )
                        } else if (loggedIn === null || loggedIn === undefined) {
                            return (
                                <Switch>

                                </Switch>
                            )
                        }
                    }}
                />
            </AppHome>
        </I18nextProvider>

    );
}

export default (withTranslation()(App));