import React, { useState, useEffect, useRef, useCallback, Fragment } from 'react';
import { withTranslation } from "react-i18next";
import ContentIndex from '../components/ContentIndex';

const Index = (props) => {

    const { t } = props;
    const isShowToastRegisterAccount = props.history.location.state?.isShowToastRegisterAccount;
    const isShowToastChangePassword = props.history.location.state?.isShowToastChangePassword;

    return (
        <Fragment>
            <ContentIndex translate={t} isShowToastRegisterAccount={isShowToastRegisterAccount} isShowToastChangePassword={isShowToastChangePassword}></ContentIndex>
        </Fragment>
    );
}

export default (withTranslation()(Index));